
@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Poppins:ital,wght@0,200;0,300;0,400;0,700;1,500;1,600&display=swap');

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
    font-family: 'Poppins';
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


.navigation-link{
    color: #545454;
    text-decoration: none;
    padding: 15px 30px;
    transition: .3s all;
}

.navigation-link:hover{
    color: #F65900;
    transition: .3s all;
}


.navigation-link.active{
    background-color: #F65900;
    color: #fff;
    text-decoration: none;
    padding: 15px 30px;
    text-align: center;
    border-radius: 50px;
    transition: .3s all;
}

.navigation-link.active:hover{
    color: #fff;
    background-color: #f78442;
    transition: .3s all;

}

.btn-custom-primary{
    background-color: #F65900;
    color: #fff;
    text-decoration: none;
    padding: 15px 40px;
    text-align: center;
    border-radius: 50px;
    transition: .3s all;
    border: none;
}


.btn-custom-primary:hover{
    color: #fff;
    background-color: #f78442;
    transition: .3s all;

}


